html, body, #root, .App {
	width: 100%;
}

body {
	overflow-x: hidden;
}

#homeContainer {
	display: grid;
	grid-template-columns: 23% 10% 34% 33%;
	grid-template-rows: 100vh 100vh 100vh;

}

#introContent {
	background-color: blue;
	background-color: #eb9321;
	border-top-right-radius: 100px; 
	border-bottom-right-radius: 100px;
	z-index: 1;
	grid-column: 1 / 3;
	grid-row: 1 / 1;
	object-fit: contain;
}

.doodleFit {
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 75% 100%;
}

#doodleImage {
	background-color: pink;
	grid-column: 1 / 5;
	grid-row: 1 / 1;
}

#secondSection {
	background-color: white;
	grid-column: 1/5;
}

#thirdSection {
	background-color: #007c9f;
	grid-column: 1/5;
	border-top-right-radius: 100px;
	border-top-left-radius: 100px;
}