@import url('https://fonts.googleapis.com/css2?family=Englebert&display=swap');

@font-face {
	font-family: 'Englebert', sans-serif;
	src: url('https://fonts.googleapis.com/css2?family=Englebert&display=swap');
}

#navig {
	height: 64px;
	display: flex;
	width: 100%;
	flex-direction: row;
	justify-content: space-between;
}

#logoDiv {
	text-align: left;
	margin: 5px 0px 0px 5px;
}

#dgLogo {
	height: 55px;
}

#navList, ul li {
	color: white;
	font-family: 'Englebert', sans-serif;
	display: inline-block;
	margin-right: 20px;
	margin-top: 20px;
}

#navList, ul {
	transform: skew(10deg);
}

#mainNav {
	margin-right: -10px;
	padding-left: 5vw;
	background-color: #eb9321;
	transform: skew(-10deg);
	border-bottom-left-radius: 50px;
}

#navList {
	text-align: right;
}

a {
	color: white;
	font-family: 'Englebert', sans-serif;
	text-decoration: none;
}