@import url(https://fonts.googleapis.com/css2?family=Englebert&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
	font-family: 'Englebert', sans-serif;
	src: url('https://fonts.googleapis.com/css2?family=Englebert&display=swap');
}

#navig {
	height: 64px;
	display: flex;
	width: 100%;
	flex-direction: row;
	justify-content: space-between;
}

#logoDiv {
	text-align: left;
	margin: 5px 0px 0px 5px;
}

#dgLogo {
	height: 55px;
}

#navList, ul li {
	color: white;
	font-family: 'Englebert', sans-serif;
	display: inline-block;
	margin-right: 20px;
	margin-top: 20px;
}

#navList, ul {
	-webkit-transform: skew(10deg);
	        transform: skew(10deg);
}

#mainNav {
	margin-right: -10px;
	padding-left: 5vw;
	background-color: #eb9321;
	-webkit-transform: skew(-10deg);
	        transform: skew(-10deg);
	border-bottom-left-radius: 50px;
}

#navList {
	text-align: right;
}

a {
	color: white;
	font-family: 'Englebert', sans-serif;
	text-decoration: none;
}
html, body, #root, .App {
	width: 100%;
}

body {
	overflow-x: hidden;
}

#homeContainer {
	display: grid;
	grid-template-columns: 23% 10% 34% 33%;
	grid-template-rows: 100vh 100vh 100vh;

}

#introContent {
	background-color: blue;
	background-color: #eb9321;
	border-top-right-radius: 100px; 
	border-bottom-right-radius: 100px;
	z-index: 1;
	grid-column: 1 / 3;
	grid-row: 1 / 1;
	object-fit: contain;
}

.doodleFit {
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 75% 100%;
}

#doodleImage {
	background-color: pink;
	grid-column: 1 / 5;
	grid-row: 1 / 1;
}

#secondSection {
	background-color: white;
	grid-column: 1/5;
}

#thirdSection {
	background-color: #007c9f;
	grid-column: 1/5;
	border-top-right-radius: 100px;
	border-top-left-radius: 100px;
}
